<template>
  <div>
    <el-row :gutter="10">
      <el-col :span="5">
        <el-input v-model="keywords" @keyup.enter.native="search" placeholder="请输入用户名称/班级名称"></el-input>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-col>
      <el-col :span="4" :offset="13">
        <el-button type="primary" @click="new_user" class="w24">新建用户</el-button>
      </el-col>
    </el-row>
    <el-row class="mtb10">
      <el-table
        :data="tableData"
        class="w24"
        @filter-change="binFilters"
        border
        :default-sort="{prop: 'user_name', order: 'descending'}"
        :row-class-name="tableRowClassName"
        ref="filterTable"
        max-height="490"
      >
        <el-table-column
          prop="user_name"
          label="用户名称"
          sortable
          header-align="center"
          align="center"
        ></el-table-column>
        <el-table-column prop="user_id" label="用户ID" header-align="center" align="center"></el-table-column>
        <el-table-column prop="user_phone" label="手机号码" header-align="center" align="center"></el-table-column>

        <el-table-column
          prop="user_sex_text"
          label="性别"
          :filters="[{ text: '男', value: '1' },
        { text: '女', value: '0' }]"
          column-key="sex"
          :filter-multiple="false"
          header-align="center"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="role_name"
          label="角色"
          :filters="filtersRoles"
          column-key="filter_group_role_id"
          header-align="center"
          align="center"
        ></el-table-column>

        <el-table-column
          prop="faculty_name"
          label="院系"
          :filters="filtersFaculties"
          column-key="filter_group_faculty_id"
          width="200px"
          header-align="center"
          align="center"
        ></el-table-column>

        <el-table-column prop="clazz_name" label="班级" header-align="center" align="center"></el-table-column>

        <el-table-column
          prop="state_id_text"
          label="状态"
          column-key="state_id"
          :filter-multiple="false"
          :filters="[{ text: '可用', value: '1' },
        { text: '停用', value: '0' }]"
          header-align="center"
          align="center"
        >
          <template slot-scope="scope">
            {{scope.row.state_id == 1?'可用':'停用'}}
            <el-tooltip
              class="tooltip_item"
              effect="dark"
              :content="scope.row.state_id == 1?'停用用户':'启用用户'"
              placement="top"
            >
              <i
                :class="scope.row.state_id == 1?'el-icon-lock red':'el-icon-unlock blue'"
                @click="handleDelete( scope.row,scope.$index,)"
              ></i>
            </el-tooltip>
          </template>
        </el-table-column>

        <el-table-column label="操作"  header-align="center" align="center">
          <template slot-scope="scope">
            <!-- <el-button size="mini" type="primary" @click="change_info_user(scope.row)">编辑</el-button> -->
            <el-tooltip class="tooltip_item" effect="dark" content="修改用户信息" placement="top">
              <i class="el-icon-edit blue" @click="change_info_user(scope.row)"></i>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row class="mt10 df jcfe">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="current_page"
        :page-sizes="[50, 100, 200]"
        :page-size="page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
// import pagination from "@/components/pagination.vue";
export default {
  // components: { pagination },
  data() {
    return {
      tableData: [], //表数据
      filtersRoles: [], //可以筛选的所有角色数组
      filtersFaculties: [], //可以筛选的所有院系数组
      filters: {
        filter_group_faculty_id: [],
        filter_group_role_id: [],
        state_id: "",
        sex: ""
      }, //已经选中的筛选数组
      keywords: "", //搜索输入
      total: 0, //总共有多少条数据
      current_page: 1, //当前选择的第几页
      page_size: 50 //当前一页选择的条数
    };
  },
  mounted() {
    this.getUsersList();
    this.$post("user/getgroupid").then(res => {
      res.roles.forEach(item => {
        item.text = item.role_name;
        item.value = item.role_id;
      });
      res.facultys.forEach(item => {
        item.text = item.faculty_name;
        item.value = item.faculty_id;
      });
      this.filtersRoles = res.roles;
      this.filtersFaculties = res.facultys;
    });
  },
  methods: {
    //搜索
    search() {
      this.current_page = 1;
      this.getUsersList();
    },
    //触发筛选
    binFilters(res) {
      if ("filter_group_role_id" in res) {
        this.filters.filter_group_role_id = res.filter_group_role_id;
      } else if ("filter_group_faculty_id" in res) {
        this.filters.filter_group_faculty_id = res.filter_group_faculty_id;
      } else if ("state_id" in res) {
        this.filters.state_id = res.state_id[0];
      } else if ("sex" in res) {
        this.filters.sex = res.sex[0];
      }
      this.current_page = 1;
      this.getUsersList();
    },
    //新建用户
    new_user() {
      this.$router.push({
        path: "/page/user/userNew",
        query: {}
      });
    },
    //修改用户信息
    change_info_user(res) {
      this.$router.push({
        path: "/page/user/userInfoChange",
        query: {}
      });
    },
    //点击功能禁用和启用触发
    handleDelete(row, index) {
      this.$confirm(
        "确定" + (row.state_id == "1" ? "停用" : "启用") + "用户?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).then(() => {
        this.$post("user/changeState", {
          user_id: row.user_id,
          state_id: row.state_id == "1" ? "0" : "1"
        }).then(res => {
          this.getUsersList();
        });
      });
    },
    //遍历过滤数据
    tableRowClassName({ row, rowIndex }) {
      Object.assign(row, row.faculty, row.clazz, row.role);
      if (row.user_sex) {
        row.user_sex_text = "男";
      } else {
        row.user_sex_text = "女";
      }
      if (row.state_id) {
        row.state_id_text = "可用";
        return "";
      } else {
        row.state_id_text = "停用";
        return "";
      }
    },
    //该表一页的大小
    handleSizeChange(res) {
      this.page_size = res;
      this.getUsersList();
    },
    //换页
    handleCurrentChange(res) {
      this.current_page = res;
      this.getUsersList();
    },
    //筛选列表请求或者普通列表请求
    getUsersList() {
      this.$post("user/getuser", {
        ...this.filters,
        keywords: this.keywords,
        page: this.current_page,
        paging: this.page_size
      }).then(res => {
        this.tableData = res.table_data;
        this.total = res.total;
      });
    }
  }
};
</script>

<style>
.red {
  font-size: 20px;
  margin-left: 17px;
  color: #fe6c6f;
}
.blue {
  font-size: 20px;
  margin-left: 17px;
  color: #409eff;
}

.tooltip_item {
  cursor: pointer;
}
</style>


